*{
    padding: 0;
    margin: 0;
/* box-sizing: border-box; */
}

.swiper {
    width: 100%;
    height: 100%;
}

.swiper-slide.mySwiperMain {
    text-align: center;
    font-size: 18px;
    background: #fff;
    max-width: 540px;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    
}

.swiper-button-prev::after,
.swiper-button-next::after {
    color: grey;
    font-size: 30px;

}

.container {
 text-align: center;
    display: flex;
    justify-content: center ;
    align-items: center ;
    box-sizing: border-box;
    justify-content: space-between;
    align-items: stretch;
    max-width: 1110px;
   
    margin: 2% auto;
    /* padding: 0 2% 0 2%; */
 
    
       -webkit-box-pack: center;
